//  The main entry of website project 入口文件
import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import elementPlus from 'element-plus'
import 'element-plus/dist/index.css'
// // Import Element UI
// import {Button} from 'element-ui'
// Useful to normalize html tags display in different web browser
import 'normalize.css'
// import css style files for all other web pages use (including components)
import './style/base.scss'
import './style/homeHeader.scss'
import './style/sideProductsCatalog.scss'
import './style/customerInfoWrap.scss'
import './style/homeNav.scss'
import './style/homeSearch.scss'
import './style/tags.scss'
import './style/homeProductsDisplay.scss'
import './plugins/element.js'

//  The main entry file creates an object 'App'
//  The object 'App' uses plugins 'router' & 'store' 
//  The object is mounted on node '#app'
//  The object is to run ---> 'App.vue'
createApp(App).use(store).use(router).use(elementPlus).mount('#app')
